*{
 margin: 0;
 padding: 0;
 box-sizing: border-box;
 
}
::selection{
  img{
    background-color: transparent;
  }
  background-color: #4286f488 !important; 
}
body{
 font-size: 62.5%;
 

background-color: #12121260;
background-color: rgba(126, 180, 226, 0.271);
background-blend-mode: multiply;
}

html{
 font-size: 1.6rem;
}
.sm-screen{
 display: none;
}
@media screen and (max-width:756px) {
 .bg-screen{
  display: none !important;
 }
 .sm-screen{
  @include grid(center);
  width: 100vw;
  height: 100vh;
  h2{
   font-size: 1.5rem !important;
   text-align: center;
  }
 }
}
